import { Location } from './../../classes/location';
import { Contact } from './../../classes/contact';
import { ModelService } from './../../services/model.service';
import { Component, OnInit } from '@angular/core';
import { LocationData } from '../../classes/location-data';
import * as _ from 'lodash';

@Component({
  selector: 'app-footer',
  templateUrl: 'footer.component.html',
  styleUrls: ['./footer.component.scss']
})
export class FooterComponent implements OnInit {

  location: Location;
  name: string;
  email: string;
  phone: string;
  facebook: string;
  youtube: string;
  footerContentLoaded = false;
  mainPageContentLoaded = false;

  constructor(private modelService: ModelService) { }

  ngOnInit() {
    this.modelService.isPageLoaded().subscribe(
      (x) => {
        this.mainPageContentLoaded = x;
      });

    Promise.all([this.modelService.getContact(), this.modelService.getLocationByInitials('YW')]).then((results: any) => {
      const contact = results[0];
      this.name = 'Arlette Cassin-Reschke'; // contact.name;
      this.email = 'yogaraum.wettersbach@gmail.com'; // contact.email;
      this.phone = '0721 9453483'; // contact.phone;
      this.location = results[1];
      this.footerContentLoaded = true;
    });
  }

}
