import { ZenkitCollections } from './../../../shared/constants/zenkit-collections';
import { Component, OnInit } from '@angular/core';
import { ModelService } from '../../../services/model.service';
import { DynamicContentService } from '../../../services/dynamic-content.service';
import { LocationData } from '../../../classes/location-data';
import { Location } from '../../../classes/location';
import { ActivatedRoute } from '@angular/router';
import * as _ from 'lodash';
import { SafeResourceUrl, DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-location',
  templateUrl: './location.component.html',
  styleUrls: ['./location.component.scss']
})
export class LocationComponent implements OnInit {

  location: Location;
  private sub: any;
  locationShortId: number;
  locationsListShortId = ZenkitCollections.locations.shortId;

  constructor(
    private modelService: ModelService,
    private dynamicContentService: DynamicContentService,
    private route: ActivatedRoute,
    private domSanitizer: DomSanitizer
  ) { }

  ngOnInit() {
    this.modelService.setPageLoaded(false);
    this.sub = this.route.params.subscribe(params => {
      this.locationShortId = params['shortId'];
      this.modelService.getLocationByShortId(this.locationShortId).then((location: Location) => {
        this.location = location;
        this.modelService.setPageLoaded(true);
      });
   });
  }

  getFileSrc(file) {
    return this.dynamicContentService.getFileSrc(_.get(file, ['shortId']), this.locationsListShortId);
  }

  getBackgroundStyle(image) {
    return {
      'background-image': 'url(' + this.getFileSrc(image) + ')'
    };
  }

  getSafeUrl(url) {
    const test = this.domSanitizer.bypassSecurityTrustResourceUrl(url);
    return test;
  }
}
