import { Component, OnInit } from '@angular/core';
import { ModelService } from '../../services/model.service';
import { Router } from '@angular/router';


@Component({
  selector: 'app-header',
  templateUrl: 'header.component.html',
  styleUrls: ['header.component.scss']
})
export class HeaderComponent implements OnInit {

  isMobileNavOpen = false;
  pageLoaded;
  pageInitiallyLoaded;
  pageIsHome;
  isBrowser;
  scrolledToTop = true;
  last_known_scroll_position: number;
  scrolledToBottom: boolean;
  updateNavigationIsRunning = false;

  pageSections: PageSection[] = [
    {
      displayName: 'Unser Service',
      ankerId: 'anker-service',
      domId: 'navbar-element-service',
      isHighlighted: false,
      scrollYStart: -1,
      scrollYEnd: -1
    },
    {
      displayName: 'Ihr Vorteil',
      ankerId: 'anker-advantages',
      domId: 'navbar-element-advantages',
      isHighlighted: false,
      scrollYStart: -1,
      scrollYEnd: -1
    },
    {
      displayName: 'Ablauf',
      ankerId: 'anker-process',
      domId: 'navbar-element-advantages',
      isHighlighted: false,
      scrollYStart: -1,
      scrollYEnd: -1
    },
    {
      displayName: 'Datenschutz',
      ankerId: 'anker-privacy',
      domId: 'navbar-element-privacy',
      isHighlighted: false,
      scrollYStart: -1,
      scrollYEnd: -1
    },
    {
      displayName: 'ber uns',
      ankerId: 'anker-about-us',
      domId: 'navbar-element-about-us',
      isHighlighted: false,
      scrollYStart: -1,
      scrollYEnd: -1
    },
    {
      displayName: 'Kontakt',
      ankerId: 'anker-contact',
      domId: 'navbar-element-contact',
      isHighlighted: false,
      scrollYStart: -1,
      scrollYEnd: -1
    }
  ];

  constructor(
    private modelService: ModelService,
    private router: Router
  ) { }

  ngOnInit() {
    this.isBrowser = this.modelService.isPlatformBrowser();
    if (this.isBrowser) {
      this.pageLoaded = true; // true
      this.pageInitiallyLoaded = false;
    } else {
      this.pageLoaded = true;
      this.pageInitiallyLoaded = true;
    }

    this.modelService.isPageLoaded().subscribe(
      (x) => {
        if (this.modelService.isPlatformBrowser()) {
          this.pageLoaded = x;
          if (x && !this.pageInitiallyLoaded) {
            this.pageInitiallyLoaded = true;
          }
          this.setScrollPositions();
          if (!this.updateNavigationIsRunning) {
            this.updateNavigation();
          }
        }
      }
    );

    this.router.events.subscribe((evt: any) => {
      this.pageIsHome = evt.url === '/';
    });

    if (this.modelService.isPlatformBrowser()) {
      window.addEventListener('scroll', (e) => {
        this.setScrollPositions();
        this.updateHighlightedPageSection();
      });
      window.addEventListener('resize', (e) => {
        this.setScrollPositions();
        this.setPageSectionPositions();
        this.updateHighlightedPageSection();
      });
    }
  }

  updateNavigation() {
    setTimeout(() => {
      this.setScrollPositions();
      this.setPageSectionPositions();
      this.updateHighlightedPageSection();
      this.updateNavigation();
    }, 1000);
  }

  setScrollPositions() {
    this.last_known_scroll_position = window.scrollY;
    console.log(window.scrollY);
    this.scrolledToTop = this.last_known_scroll_position === 0;
    const lastElement = this.pageSections[this.pageSections.length - 1];
    if (lastElement.scrollYEnd) {
      this.scrolledToBottom = ((window.innerHeight + window.scrollY) >= lastElement.scrollYEnd);
    }
  }

  setPageSectionPositions() {
    this.pageSections.forEach((pageSection) => {
      const element = document.getElementById(pageSection.ankerId);
      if (element) {
        pageSection.scrollYStart = element.offsetTop - 100;
        pageSection.scrollYEnd = element.offsetTop + element.clientHeight - 100;
      }
    });
  }

  updateHighlightedPageSection() {
    this.pageSections.forEach((pageSection: PageSection) => {
      if (pageSection.scrollYStart && pageSection.scrollYEnd) {
        if (this.last_known_scroll_position >= pageSection.scrollYStart
          && this.last_known_scroll_position <= pageSection.scrollYEnd
           && !this.scrolledToBottom) {
          pageSection.isHighlighted = true;
        } else {
          pageSection.isHighlighted = false;
        }
      }

      if (this.scrolledToBottom) {
        this.pageSections[this.pageSections.length - 1].isHighlighted = true;
      }
    });
  }

  toggleMobileNav() {
    this.isMobileNavOpen = !this.isMobileNavOpen;
  }

  closeMobileNav() {
    this.isMobileNavOpen = false;
  }

  scrollIntoView(elementId: string) {
    this.isMobileNavOpen = false;
    const element = document.getElementById(elementId);
    element.scrollIntoView({ behavior: 'smooth', block: 'start'});
  }

}


export interface PageSection {
  displayName: string;
  ankerId: string;
  domId: string;
  isHighlighted: boolean;
  scrollYStart: number;
  scrollYEnd: number;
}
